<template>
    <div class="rating">
        <template v-if="full===0">
            <span>暂无评分</span>
        </template>
        <template v-else>
            <template v-for="n in full">
                <span class="star-full"></span>
            </template>
            <template v-for="n in half">
                <span class="star-half"></span>
            </template>
            <template v-for="n in gray">
                <span class="star-gray"></span>
            </template>
            <span class="average">{{rating.average}}</span>
        </template>
        <slot name="ratingsCount"></slot>
    </div>
</template>

<script>
export default {
    props:['rating'],
    data(){
        return {
            full:0,
            half:0,
            gray:0
        }
    },
    created(){
        let average = Number(this.rating.average) 
        //总分
        this.full = parseInt(average/2)
        //总分的星数
        this.half = average%2 === 0?0:1
        //判断是否有半颗心存在
        this.gray = 5 - this.full -this.half 
        //这里是灰心的指数
    }
}
</script>

<style lang="scss" scoped>
    .rating {
        margin-top: .4rem;
        font-size: 1.4rem;
        color: #aaa;
    }
    .star-full {
        display: inline-block;
        width: 1.3rem;
        height: 1.3rem;
        background-image: url(data:image/png;base64,);
        background-size: 1.3rem 1.3rem;
    }
    .star-gray {
        display: inline-block;
        width: 1.3rem;
        height: 1.3rem;
        background-image: url(data:image/png;base64,);
        background-size: 1.3rem;
    }
    .star-half {
        display: inline-block;
        width: 1.3rem;
        height: 1.3rem;
        background-image: url(data:image/png;base64,);
        background-size: 1.3rem 1.3rem;
    }
    .average {
        padding-right: 0.8rem;
    }
</style>



